:root{
    --primary: #fff;
}

.btn{
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
    padding-bottom: 10px;
}

.btn--primary{
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-out;
}

.btn--primarys{
    background-color: black;
    color: #242424;
    border: 1px solid black;
}

.btn--outlines{
    background-color: transparent;
    color: black;
    padding: 8px 20px;
    border: 1px solid black;
    transition: all 0.3s ease-out;
}

.btn--mediums{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--larges{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mediums:hover, .btn--larges:hover{
    background: black;
    color: white;
    transition: all 0.3s ease-out;
}
